<template>
<keep-alive>
  <div v-if="two_rates[0].Rate">
    <h1 class="page-title">{{ $t('__exchangePage_title') }}</h1> 
    <div class="card convert-card" >
      <div class="card-body">
        <h5 class="card-title"> 
          <span v-html=" $t('__All_currency['+`'${two_rates[0].Rate}'`+'].currency') "></span> 
          {{ $t('__homePage_exchange') }}
          <span v-html=" $t('__All_currency['+`'${two_rates[1].Rate}'`+'].currency') "></span>
        </h5>
        <div class="card-text">
          <p class="card-exrate-title">{{ $t('__homePage_fast_exchange' )}}</p>
          <!-- 快速轉換  -->
          <!-- exchange_to  -->
          <p>
            1 <span class="exrate-item" v-html=" $t('__All_currency['+`'${two_rates[0].Rate}'`+'].currency') "></span>  
            <span class="exrate-arrow">⇛</span>  
            <span class="exrate">
              {{ two_rates[1].Exrate / two_rates[0].Exrate | float_currency }} 
              <span v-html=" $t('__All_currency['+`'${two_rates[1].Rate}'`+'].currency') "></span>
            </span>
          </p>
          <!-- reverse_to_exchange -->
          <p> 
            1 <span class="exrate-item" v-html=" $t('__All_currency['+`'${two_rates[1].Rate}'`+'].currency') "></span> 
            <span class="exrate-arrow">⇛</span>  
            <span class="exrate">
              {{ two_rates[0].Exrate / two_rates[1].Exrate | float_currency }}     
              <span v-html=" $t('__All_currency['+`'${two_rates[0].Rate}'`+'].currency') "></span>
            </span>
          </p>
          <p class="card-exrate-title">{{ $t('__homePage_handler_exchange') }}</p>
          <!-- 快速轉換 end  -->

          <!-- 手動轉換  -->
          <!-- exchange_to  -->
          <p>
            <input 
              type="number" 
              :id="two_rates[0].Rate" 
              v-model="to_exrate"
              pattern="[0-9]*"
            ><label :for="two_rates[0].Rate" class="exrate-item" v-html=" $t('__All_currency['+`'${two_rates[0].Rate}'`+'].currency') "></label> 
            <span class="exrate-arrow">⇛</span>  
            <span class="exrate">
              {{ two_rates[1].Exrate / two_rates[0].Exrate * to_exrate | handler_float_currency }}  
              <span v-html=" $t('__All_currency['+`'${two_rates[1].Rate}'`+'].currency') "></span>
            </span>
          </p>
          <!-- reverse_to_exchange -->
          <p>
            <input 
              type="number" 
              :id="two_rates[1].Rate" 
              v-model="for_exrate"
              pattern="[0-9]*"
            ><label :for="two_rates[1].Rate" class="exrate-item" v-html=" $t('__All_currency['+`'${two_rates[1].Rate}'`+'].currency') "></label> 
            <span class="exrate-arrow">⇛</span>  
            <span class="exrate">
              {{ two_rates[0].Exrate / two_rates[1].Exrate  * for_exrate | handler_float_currency }}  
              <span v-html=" $t('__All_currency['+`'${two_rates[0].Rate}'`+'].currency') "></span>
            </span> 
          </p>
          <!-- 手動轉換 end -->
          <div class="see-more">
            <button 
              class="btn btn-primary card-btn"
              @click="Back_router"
            >返回上一頁</button>
          </div>
        </div>       
      </div>
      <div class="card-footer">
        <small class="text-muted">{{ $t('__homePage_slick_updateTime') }}：{{ two_rates[0].UTC }}</small>
      </div>
    </div>
  </div>
  </keep-alive>
</template>

<script>
const Cryptr = require('cryptr');
const cryptr = new Cryptr('myTotalySecretKey');
export default {
  name:'Convert',
  data:()=>({
    rates :[],
    to_exrate:1,
    for_exrate:1,
    Json_decry:{},
  }),
  computed:{
    two_rates(){
      this.rates[0]={
        Rate :this.Json_decry.IN_Rate,
        Exrate : this.Json_decry.IN_Exrate,
        UTC : this.Json_decry.UTC
      }
      this.rates[1]={
        Rate :this.Json_decry.OUT_Rate,
        Exrate : this.Json_decry.OUT_Exrate,
      }
      return this.rates
    }
  },
  methods:{
    Back_router(){
      this.$router.back()
    },
    CHECK_query(){
      try{
        const decryptedString =cryptr.decrypt(this.$route.query.encryptedString);
        this.Json_decry = JSON.parse(decryptedString);
      }
      catch(err) {
        this.$router.push({name:'Exchange'})
        alert('找不到兌換貨幣！')
      }
    }
  },
  watch:{
    $route(to) {
      this.CHECK_query()
    }
  },
  mounted(){
    this.CHECK_query()
  }
}
</script>


